*{
	list-style-type: none;
	margin:0;
	padding:0;
	box-sizing: border-box;
    outline: none;
}
section{
	background-color: rgba(0,0,0,0.5);
	display: flex;
	flex-direction: column;
	margin:0 auto;
	font-family: 'PT Sans', sans-serif;
	opacity: 1;
}
section>div{
	background-color: white;
	opacity: 1;
	position: relative;
    width: 45%;
    margin: 7% auto 0;
    padding: 24px 37px 50px;
    overflow: hidden;
    background: #fff;
}
section>div>.iconpart{
	text-align: right;
	margin-right: -4%;
}
section>div>.iconpart:hover{
	cursor: pointer;
	opacity: .7;
}
section>div>.iconpart>i{
	font-size: 128%;
}
section>div>h2{
	font-size: 28px;
    line-height: 1;
    padding: 0 0 48px;
    text-align: center;
}
section form{
	display: flex;
	flex-direction: column;
	margin:0 auto;
}
section form>label{    
	display: inline-block;
    line-height: 1.4;
    font-size: 100%;
    padding-top: 5px;
    padding-bottom: 8px;
    color: #282828;
}
section form>input{
	color: #282828;
    border-color: #ccc;
    background-color: transparent;
    width: 100%;
    font-family: inherit;
    line-height: 1.6;
    padding: 8px 10px;
    border: 1px solid #e1e1e1;
    margin-bottom: 18px;
}
section form>.saveorlost{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 25px;
	margin-top: 8px;

}
section .firstbutton{
	background-color: black;
	border:none;
	color:white;
	padding: 12px;
	font-size: 16px;
	text-align: center;
    opacity: 1;
    transition: opacity 0.2s ease;
    cursor: pointer;
}
section .firstbutton:hover{
	color: #fff;
	opacity: .82;
}
section .or{
	display: flex;
	flex-direction: row;
	align-items: baseline;
	margin: 19px 0;
}
section .or>div{
	border-bottom: 1px solid #ccc;
	width: 50%;
}
section .or>span{
	margin:10px 10px;
	background: #fff;
}
 .secondbutton{
	background-color: #fff;
	border: 1px solid #ccc;
	color:black;
	padding: 12px;
	font-size: 16px;
	text-align: center;
    opacity: 1;
    transition: opacity 0.2s ease;
    cursor: pointer;
}
 .secondbutton:hover{
	border-color: black;
	opacity: .82;
}
 button>a{
	text-decoration: none;
	color:black;
}
#divcolor, #div{
    color:red  !important;
    font-style: italic  !important;
}